help;\n}\n\n//\n// Pre, Code, and User Input\n// -------------------------\n\ncode,\npre,\nkbd,\nsamp {\n  font-family: $monoFont;\n  font-size: em(14);\n}\n\ncode {\n  vertical-align: middle;\n  white-space: nowrap;\n}\n\npre code {\n  white-space: pre;\n}\n\nkbd {\n  display: inline-block;\n  line-height: 1.75;\n  white-space: nowrap;\n}\n\nsamp {\n  vertical-align: bottom;\n}\n\n//\n// Addresses\n// ---------\n\naddress {\n  font-style: normal;\n}\n\np {\n  font-size: em(16);\n  line-height: em(24, 16);\n\n  &.lead {\n    font-size: em(20);\n    line-height: em(28, 20);\n  }\n}","////\n/// @group size-functions\n/// @since 2.0.1\n/// @author Bernhard Ruoff\n/// @access public\n////\n\n/// Converts a px value into a rem value\n/// @example\n/// width: rem(32); // returns 2rem\n///\n/// @param {Number} $value - value in pixels\n/// @param {Number} $base [$base-size]- 16 !default\n/// @require {variable} $base-size\n///\n/// @return {Number} Value in rem\n\n@function rem($value, $base: $base-size) {\n  @return ($value / $base) + rem;\n}","////\n/// @group size-functions\n/// @since 2.0.1\n/// @author Bernhard Ruoff\n/// @access public\n////\n\n/// Converts a px value into a em value\n/// @example\n/// font-size: em(16); // returns 1em\n/// line-height: em(24, 12); // returns 2em\n///\n/// @param {Number} $value - value in pixels\n/// @param {Number} $base [$base-size]- 16 !default\n/// @require {variable} $base-size\n///\n/// @return {Number} Value in em\n\n@function em($value, $base: $base-size) {\n  @return ($value / $base) + em;\n}","// Type Helper classes\n\n.text-center {\n  text-align: center;\n}\n\n.text-right {\n  text-align: right;\n}\n\n.text-left {\n  text-align: left;\n}","//\n// Headings\n// =============================================================================\n\nh1, h2, h3, h4, h5, h6,\n.h1, .h2, .h3, .h4, .h5, .h6,\n.giga, .mega, .kilo {\n  font-weight: $headings-weight;\n\n  small,\n  .small {\n    vertical-align: baseline;\n  }\n}\n\nh1, h2, h3,\n.h1, .h2, .h3,\n.giga, .mega, .kilo {\n  margin: rem(16) 0 rem(8);\n\n  small,\n  .small {\n    font-size: 65%;\n  }\n}\n\nh4, h5, h6,\n.h4, .h5, .h6 {\n  margin: rem(16) 0 rem(8);\n\n  small,\n  .small {\n    font-size: 75%;\n  }\n}\n\n// Ratio for downscaling on mobile devices\n$ratioXS: .875;\n\nh1,\n.h1,\n.giga {\n  font-size: em($h1-size * $ratioXS);\n  line-height: em(($h1-height * $ratioXS), ($h1-size * $ratioXS));\n\n  @media (min-width: $bpSm) {\n    font-size: em($h1-size);\n    line-height: em($h1-height, $h1-size);\n  }\n}\n\nh2,\n.h2 {\n  font-size: em($h2-size * $ratioXS);\n  line-height: em(($h2-height * $ratioXS), ($h2-size * $ratioXS));\n\n  @media (min-width: $bpSm) {\n    font-size: em($h2-size);\n    line-height: em($h2-height, $h2-size);\n  }\n}\n\nh3,\n.h3,\n.mega {\n  font-size: em($h3-size * $ratioXS);\n  line-height: em(($h3-height * $ratioXS), ($h3-size * $ratioXS));\n\n  @media (min-width: $bpSm) {\n    font-size: em($h3-size);\n    line-height: em($h3-height, $h3-size);\n  }\n}\n\nh4,\n.h4 {\n  font-size: em($h4-size * $ratioXS);\n  line-height: em(($h4-height * $ratioXS), ($h4-size * $ratioXS));\n\n  @media (min-width: $bpSm) {\n    font-size: em($h4-size);\n    line-height: em($h4-height, $h4-size);\n  }\n}\n\nh5,\n.h5,\n.kilo {\n  font-size: em($h5-size * $ratioXS);\n  line-height: em(($h5-height * $ratioXS), ($h5-size * $ratioXS));\n\n  @media (min-width: $bpSm) {\n    font-size: em($h5-size);\n    line-height: em($h5-height, $h5-size);\n  }\n}\n\nh6,\n.h6 {\n  font-size: em($h6-size * $ratioXS);\n  line-height: em(($h6-height * $ratioXS), ($h6-size * $ratioXS));\n\n  @media (min-width: $bpSm) {\n    font-size: em($h6-size);\n    line-height: em($h6-height, $h6-size);\n  }\n}","table {\n  width: 100%;\n  border-collapse: collapse;\n  border-spacing: 0;\n}\n","@mixin clearfix() {\n  &:before,\n  &:after {\n    content: \"\";\n    display: table;\n  }\n  &:after {\n    clear: both;\n  }\n}","////\n/// dl.scss\n/// @deprecated\n\nd