24);\n        padding-right: rem(8);\n      }\n\n      &:empty {\n        margin-left: rem(-8);\n        margin-right: rem(-8);\n      }\n    }\n  }\n\n  &.big {\n    font-size: rem(20);\n    line-height: rem(28);\n    padding: rem(10) rem(16);\n\n    .ec-icon {\n      &:before {\n        font-size: rem(28);\n        line-height: rem(28);\n      }\n    }\n  }\n\n  &.block {\n    width: 100%;\n    display: block;\n    margin-left: 0;\n    margin-right: 0;\n  }\n}","////\n/// @group shadow\n/// @since 2.0.1\n/// @author Bernhard Ruoff\n/// @access public\n////\n\n/// cross-browser mixin for box-shadow\n/// @param {value} $value - can be multiple arguments\n///\n/// @example\n/// box-shadow(0 4px 16px rgba(black, .2), 0 4px 32px rgba(red, .1));\n\n@mixin box-shadow($value...) {\n  -webkit-box-shadow: $value;\n  -moz-box-shadow: $value;\n  box-shadow: $value;\n}","img{\n  &.responsive{\n    width: 100%;\n    height: auto;\n  }\n}","figure {\n  margin: 0;\n}",".pull-right {\n  float: right !important;\n}\n\n.pull-left {\n  float: left !important;\n}","$base-padding: rem(16);\n\n.padding {\n  padding: $base-padding;\n}\n\n.padding-top {\n  padding-top: $base-padding;\n}\n\n.padding-right {\n  padding-right: $base-padding;\n}\n\n.padding-bottom {\n  padding-bottom: $base-padding;\n}\n\n.padding-left {\n  padding-left: $base-padding;\n}\n\n.padding-horizontal {\n  padding-top: $base-padding;\n  padding-bottom: $base-padding;\n}\n\n.padding-vertical {\n  padding-left: $base-padding;\n  padding-right: $base-padding;\n}","ul.unstyled,\nol.unstyled {\n  margin: 0;\n  padding: 0;\n  list-style: none;\n}\n\nul.inline,\nol.inline {\n  margin: 0;\n  padding: 0;\n  list-style: none;\n\n  > li {\n    display: inline-block;\n  }\n}\n\n","%shadow {\n\n  &%dropdown {\n    @include box-shadow(0 4px 8px rgba(black, 0.1), 0 4px 24px rgba(black, 0.1));\n  }\n\n  &%overlay {\n    @include box-shadow(0 4px 8px rgba(black, 0.1), 0 4px 24px rgba(black, 0.1));\n  }\n}\n","%pop {\n  position: fixed;\n  top: 50%;\n  left: 50%;\n  width: 50vw;\n  max-width: 480px;\n  min-width: 240px;\n  @include transform(translate3d(-50%, -50%, 0));\n  @extend %shadow%overlay;\n  padding: 8px;\n  @include border-radius($dropdown-radius);\n  z-index: 9999;\n  background-color: getColor(base, background);\n  min-height: 48px;\n}","// generic transform\n@mixin transform($transforms) {\n  -moz-transform: $transforms;\n  -o-transform: $transforms;\n  -ms-transform: $transforms;\n  -webkit-transform: $transforms;\n  transform: $transforms;\n  -webkit-backface-visibility: hidden;\n}\n\n@mixin transform-style($val) {\n  -webkit-transform-style: $val;\n  -moz-transform-style: $val;\n  -ms-transform-style: $val;\n  transform-style: $val;\n}\n\n//transform origin\n@mixin transform-origin($origin) {\n  moz-transform-origin: $origin;\n  -o-transform-origin: $origin;\n  -ms-transform-origin: $origin;\n  -webkit-transform-origin: $origin;\n  transform-origin: $origin;\n}","////\n/// @group border-radius\n/// @since 2.0.1\n/// @author Bernhard Ruoff\n/// @access public\n////\n\n/// cross-browser mixin for border-radius\n/// @param {value} $radius - can be multiple arguments\n\n@mixin border-radius($radius) {\n  -webkit-border-radius: $radius;\n  border-radius: $radius;\n  // stops bg color from leaking outside the border:\n  background-clip: padding-box;\n}","/// tooltip - 1.0.0\n\n%tooltip {\n  background-color: rgba(getColor(background, invert), .8);\n  color: getColor(text, invert);\n  position: absolute;\n  display: inline-block;\n  font-size: 10px;\n  line-height: 20px;\n  padding: 0 8px;\n  @include ellipsis;\n  max-width: 160px;\n  @include border-radius(4px);\n  z-index: 10;\n}\n\n[data-tooltip] {\n  position: relative;\n\n  &:before {\n    visibility: hidden;\n    opacity: 0;\n    content: attr(data-tooltip);\n    @extend %tooltip;\n    @include transition(opacity .4s, transform .4s);\n  }\n\n  &:hover {\n    &:before {\n      visibility: visible;\n      opacity: 1;\n    }\n  }\n}\n\n[data-tooltip-position=left] {\n  &:before {\n    right: 100%;\n    top: 50%;\n    margin-top: -10px;\n    margin-right: 4px;\n    @include transform(translateX(-16px));\n  }\n\n  &:hover {\n    &:before {\n      @include transform(translateX(0));\n    }\n  }\n}\n\n[data-tooltip-position=right] {\n  &:before {\n    left: 100%;\n    top: 50%;\n    margin-top: -10px;\n    margin-left: 4px;\n    @include transform(translateX(16px));\n  }\n\n  &:hover {\n    &:before {\n      @include transform(translateX(0));\n    }\n  }\n}\n\n[data-tooltip-position=top] {\n  &:before {\n    bottom: 100%;\n    left: 50%;\n    margin-bottom: 4px;\n    @include transform(translate3D(-50%, 16px, 0));\n  }\n\n  &:hover {\n    &:before {\n      @include transform(translate3D(-50%, 0, 0));\n    }\n  }\n}\n\n[data-tooltip-position=bottom] {\n  &:before {\n    top: 100%;\n    left: 50%;\n    margin-top: 4px;\n    @include transform(translate3D(-50%, -16px, 0));\n  }\n\n  &:hover {\n    &:before {\n      @include transform(translate3D(-50%, 0, 0));\n    }\n  }\n}\n\n[data-tooltip-delay] {\n  &:before {\n    @include transition-delay(.4s);\n  }\n}","@mixin ellipsis() {\n  -ms-text-overflow: ellipsis;\n  text-overflow: ellipsis;\n  overflow: hidden;\n  white-space: nowrap;\n}\n","@mixin transition($transition...) {\n  -moz-transition: $transition;\n  -o-transition: $transition;\n  -webkit-transition: $transition;\n  transition: $transition;\n}\n\n@mixin transition-property($property...) {\n  -moz-transition-property: $property;\n  -o-transition-property: $property;\n  -webkit-transition-property: $property;\n  transition-property: $property;\n}\n\n@mixin transition-duration($duration...) {\n  -moz-transition-property: $duration;\n  -o-transition-property: $duration;\n  -webkit-transition-property: $duration;\n  transition-property: $duration;\n}\n\n@mixin transition-delay($delay...) {\n  -moz-transition-delay: $delay;\n  -o-transition-delay: $delay;\n  -webkit-transition-delay: $delay;\n  transition-delay: $delay;\n}","ul.vcms-dropdown {\n  @extend ul.unstyled;\n  outline: none;\n\n  > li {\n    white-space: nowrap;\n    position: relative;\n    outline: none;\n    cursor: pointer;\n\n    &:after {\n      content: '';\n      position: absolute;\n      bottom: 2px;\n      right: 2px;\n      border: 4px solid transparent;\n      border-top-color: rgba(black,.2);\n      @include transform(rotateZ(-45deg));\n\n    }\n\n    > a {\n      &:focus {\n        & + ul.dropdown-options {\n          display: inherit;\n          opacity: 1;\n        }\n      }\n    }\n\n    &:focus {\n\n      ul.vcms-dropdown-options {\n        display: inherit;\n        opacity: 1;\n      }\n    }\n  }\n\n  &:not(.focus-only) {\n    > li {\n      position: relative;\n      outline: none;\n      cursor: pointer;\n\n      > a {\n        &:hover,\n        &:focus {\n          & + ul.dropdown-options {\n            display: inherit;\n            opacity: 1;\n          }\n        }\n      }\n\n      &:hover,\n      &:focus {\n        ul.dropdown-options {\n          display: inherit;\n          opacity: 1;\n        }\n      }\n    }\n  }\n\n  ul.vcms-dropdown-options {\n    position: absolute;\n    top: 100%;\n    background-color: getColor(base, background);\n    @include border-radius($dropdown-radius * 1px);\n    @extend %shadow%dropdown;\n    @include transition(opacity .4s);\n    max-height: 320px;\n    overflow-y: auto;\n    display: none;\n    opacity: 0;\n    z-index: 100;\n    padding: 4px;\n    margin: 0;\n\n    li {\n      display: block;\n      text-align: left;\n\n      a {\n        width: 100%;\n      }\n\n      &.divider {\n        margin: 8px 0;\n        background-color: getColor(base, divider);\n        height: 1px;\n      }\n    }\n  }\n\n  &.center {\n    ul.vcms-dropdown-options {\n      left: 50%;\n      @include transform(translateX(-50%));\n    }\n  }\n\n  &.right {\n    ul.vcms-dropdown-options {\n      right: 0;\n    }\n  }\n\n  &.left {\n    ul.vcms-dropdown-options {\n      left: 0;\n    }\n  }\n\n  &.full {\n    ul.vcms-dropdown-options {\n      left: 0;\n      width: 100%;\n    }\n  }\n}",".vcms-editor {\n  padding: 16px;\n}",".vcms-content {\n  display: block;\n  border: 1px solid getColor(base, border);\n  outline: none;\n  @include border-radius(2px);\n  @include box-shadow(inset 0 0 4px rgba(black, .1));\n  padding: 8px;\n  width: 100%;\n  height: 100%;\n\n  [contenteditable] {\n    outline: none;\n  }\n\n  h1, h2, h3, h4, h5, h6, p, small, strong, em, i, b, u {\n    cursor: text;\n  }\n}","%toolbar {\n  background-color: getColor(base, background);\n  margin: 8px -4px;\n  font-family: $bodyFont;\n\n  nav {\n    @include flexbox;\n    @include flex-wrap(wrap);\n\n    ul.vcms-toolbar-group {\n      margin: 4px;\n      @include flex(0 0 auto);\n\n      > li {\n        display: inline-block;\n        position: relative;\n\n        &:first-child {\n          a {\n            @include border-radius(4px 0 0 4px);\n          }\n        }\n\n        &:last-child {\n          a {\n            @include border-radius(0 4px 4px 0);\n          }\n        }\n\n        .active {\n          > a {\n            background-color: getColor(state, active);\n            font-weight: bold;\n            color: setColor(getColor(state, active));\n          }\n        }\n\n        a {\n          color: getColor(text, light);\n          display: inline-block;\n          min-width: 40px;\n          height: 40px;\n          font-size: 14px;\n          line-height: 24px;\n          padding: 8px;\n          text-align: center;\n          cursor: pointer;\n          @include user-select(none);\n          background-color: getColor(background, lightest);\n\n          &:hover {\n            background-color: getColor(state, hover);\n            color: setColor(getColor(state, hover));\n          }\n        }\n\n        .vcms-tags {\n          > li {\n            > a {\n              font-weight: bold;\n              color: getColor(base, text);\n              padding: 8px 16px;\n            }\n          }\n        }\n      }\n    }\n  }\n}\n\n.vcms-toolbar {\n  @extend %toolbar;\n}","//\n// @font-face\n// -------------\n@import url('https://icons.cdn.entrecode.de/ec-icons-1.1.12.css');\n\n$bodyFont: sans-serif;\n\n//\n// Color Palette\n// -------------\n\n$background: white;\n$text: #37474F;\n$link: #00B0FF;\n$super: #FF5353;\n$minor: #37474F;\n$highlight: #FFFF8D;\n\n// function for setColor\n@if (lightness($text) > 50) {\n  $setDark: $background;\n  $setLight: $text;\n} @else {\n  $setDark: $text;\n  $setLight: $background;\n}\n\n$dropdown-radius: 4;","// Flexbox Mixins\n// http://philipwalton.github.io/solved-by-flexbox/\n// https://github.com/philipwalton/solved-by-flexbox\n//\n// Copyright (c) 2013 Brian Franco\n//\n// Permission is hereby granted, free of charge, to any person obtaining a\n// copy of this software and associated documentation files (the\n// \"Software\"), to deal in the Software without restriction, including\n// without limitation the rights to use, copy, modify, merge, publish,\n// distribute, sublicense, and/or sell copies of the Software, and to\n// permit persons to whom the Software is furnished to do so, subject to\n// the following conditions:\n// The above copyright notice and this permission notice shall be included\n// in all copies or substantial portions of the Software.\n// THE SOFTWARE IS PROVIDED \"AS IS\", WITHOUT WARRANTY OF ANY KIND, EXPRESS\n// OR IMPLIED, INCLUDING BUT NOT LIMITED TO THE WARRANTIES OF\n// MERCHANTABILITY, FITNESS FOR A PARTICULAR PURPOSE AND NONINFRINGEMENT.\n// IN NO EVENT SHALL THE AUTHORS OR COPYRIGHT HOLDERS BE LIABLE FOR ANY\n// CLAIM, DAMAGES OR OTHER LIABILITY, WHETHER IN AN ACTION OF CONTRACT,\n// TORT OR OTHERWISE, ARISING FROM, OUT OF OR IN CONNECTION WITH THE\n// SOFTWARE OR THE USE OR OTHER DEALINGS IN THE SOFTWARE.\n//\n// This is a set of mixins for those who want to mess around with flexbox\n// using the native support of current browsers. For full support table\n// check: http://caniuse.com/flexbox\n//\n// Basically this will use:\n//\n// * Fallback, old syntax (IE10, mobile webkit browsers - no wrapping)\n// * Final standards syntax (FF, Safari, Chrome, IE11, Opera)\n//\n// This was inspired by:\n//\n// * http://dev.opera.com/articles/view/advanced-cross-browser-flexbox/\n//\n// With help from:\n//\n// * http://w3.org/tr/css3-flexbox/\n// * http://the-echoplex.net/flexyboxes/\n// * http://msdn.microsoft.com/en-us/library/ie/hh772069(v=vs.85).aspx\n// * http://css-tricks.com/using-flexbox/\n// * http://dev.opera.com/articles/view/advanced-cross-browser-flexbox/\n// * https://developer.mozilla.org/en-us/docs/web/guide/css/flexible_boxes\n\n//----------------------------------------------------------------------\n\n// Flexbox Containers\n//\n// The 'flex' value causes an element to generate a block-level flex\n// container box.\n//\n// The 'inline-flex' value causes an element to generate a inline-level\n// flex container box.\n//\n// display: flex | inline-flex\n//\n// http://w3.org/tr/css3-flexbox/#flex-containers\n//\n// (Placeholder selectors for each type, for those who rather @extend)\n\n@mixin flexbox {\n  display: -webkit-box;\n  display: -webkit-flex;\n  display: -moz-flex;\n  display: -ms-flexbox;\n  display: flex;\n}\n\n%flexbox {\n  @include flexbox;\n}\n\n//----------------------------------\n\n@mixin inline-flex {\n  display: -webkit-inline-box;\n  display: -webkit-inline-flex;\n  display: -moz-inline-flex;\n  display: -ms-inline-flexbox;\n  display: inline-flex;\n}\n\n%inline-flex {\n  @include inline-flex;\n}\n\n//----------------------------------------------------------------------\n\n// Flexbox Direction\n//\n// The 'flex-direction' property specifies how flex items are placed in\n// the flex container, by setting the direction of the flex container's\n// main axis. This determines the direction that flex items are laid out in.\n//\n// Values: row | row-reverse | column | column-reverse\n// Default: row\n//\n// http://w3.org/tr/css3-flexbox/#flex-direction-property\n\n@mixin flex-direction($value: row) {\n  @if $value == row-reverse {\n    -webkit-box-direction: reverse;\n    -webkit-box-orient: horizontal;\n  } @else if $value == column {\n    -webkit-box-direction: normal;\n    -webkit-box-orient: vertical;\n  } @else if $value == column-reverse {\n    -webkit-box-direction: reverse;\n    -webkit-box-orient: vertical;\n  } @else {\n    -webkit-box-direction: normal;\n    -webkit-box-orient: horizontal;\n  }\n  -webkit-flex-direction: $value;\n  -moz-flex-direction: $value;\n  -ms-flex-direction: $value;\n  flex-direction: $value;\n}\n\n// Shorter version:\n@mixin flex-dir($args...) {\n  @include flex-direction($args...);\n}\n\n//----------------------------------------------------------------------\n\n// Flexbox Wrap\n//\n// The 'flex-wrap' property controls whether the flex container is single-line\n// or multi-line, and the direction of the cross-axis, which determines\n// the direction new lines are stacked in.\n//\n// Values: nowrap | wrap | wrap-reverse\n// Default: nowrap\n//\n// http://w3.org/tr/css3-flexbox/#flex-wrap-property\n\n@mixin flex-wrap($value: nowrap) {\n  // No Webkit Box fallback.\n  -webkit-flex-wrap: $value;\n  -moz-flex-wrap: $value;\n  @if $value == nowrap {\n    -ms-flex-wrap: none;\n  } @else {\n    -ms-flex-wrap: $value;\n  }\n  flex-wrap: $value;\n}\n\n//----------------------------------------------------------------------\n\n// Flexbox Flow (shorthand)\n//\n// The 'flex-flow' property is a shorthand for setting the 'flex-direction'\n// and 'flex-wrap' properties, which together define the flex container's\n// main and cross axes.\n//\n// Values: <flex-direction> | <flex-wrap>\n// Default: row nowrap\n//\n// http://w3.org/tr/css3-flexbox/#flex-flow-property\n\n@mixin flex-flow($values: (row nowrap)) {\n  // No Webkit Box fallback.\n  -webkit-flex-flow: $values;\n  -moz-flex-flow: $values;\n  -ms-flex-flow: $values;\n  flex-flow: $values;\n}\n\n//----------------------------------------------------------------------\n\n// Flexbox Order\n//\n// The 'order' property controls the order in which flex items appear within\n// their flex container, by assigning them to ordinal groups.\n//\n// Default: 0\n//\n// http://w3.org/tr/css3-flexbox/#order-property\n\n@mixin order($int: 0) {\n  -webkit-box-ordinal-group: $int + 1;\n  -webkit-order: $int;\n  -moz-order: $int;\n  -ms-flex-order: $int;\n  order: $int;\n}\n\n//----------------------------------------------------------------------\n\n// Flexbox Grow\n//\n// The 'flex-grow' property sets the flex grow factor. Negative numbers\n// are invalid.\n//\n// Default: 0\n//\n// http://w3.org/tr/css3-flexbox/#flex-grow-property\n\n@mixin flex-grow($int: 0) {\n  -webkit-box-flex: $int;\n  -webkit-flex-grow: $int;\n  -moz-flex-grow: $int;\n  -ms-flex-positive: $int;\n  flex-grow: $int;\n}\n\n//----------------------------------------------------------------------\n\n// Flexbox Shrink\n//\n// The 'flex-shrink' property sets the flex shrink factor. Negative numbers\n// are invalid.\n//\n// Default: 1\n//\n// http://w3.org/tr/css3-flexbox/#flex-shrink-property\n\n@mixin flex-shrink($int: 1) {\n  -webkit-flex-shrink: $int;\n  -moz-flex-shrink: $int;\n  -ms-flex-negative: $int;\n  flex-shrink: $int;\n}\n\n//----------------------------------------------------------------------\n\n// Flexbox Basis\n//\n// The 'flex-basis' property sets the flex basis. Negative lengths are invalid.\n//\n// Values: Like \"width\"\n// Default: auto\n//\n// http://www.w3.org/TR/css3-flexbox/#flex-basis-property\n\n@mixin flex-basis($value: auto) {\n  -webkit-flex-basis: $value;\n  -moz-flex-basis: $value;\n  -ms-flex-preferred-size: $value;\n  flex-basis: $value;\n}\n\n//----------------------------------------------------------------------\n\n// Flexbox \"Flex\" (shorthand)\n//\n// The 'flex' property specifies the components of a flexible length: the\n// flex grow factor and flex shrink factor, and the flex basis. When an\n// element is a flex item, 'flex' is consulted instead of the main size\n// property to determine the main size of the element. If an element is\n// not a flex item, 'flex' has no effect.\n//\n// Values: none | <flex-grow> <flex-shrink> || <flex-basis>\n// Default: See individual properties (1 1 0).\n//\n// http://w3.org/tr/css3-flexbox/#flex-property\n\n@mixin flex($fg: 1, $fs: null, $fb: null) {\n\n  // Set a variable to be used by box-flex properties\n  $fg-boxflex: $fg;\n\n  // Box-Flex only supports a flex-grow value so let's grab the\n  // first item in the list and just return that.\n  @if type-of($fg) == 'list' {\n    $fg-boxflex: nth($fg, 1);\n  }\n\n  -webkit-box-flex: $fg-boxflex;\n  -webkit-flex: $fg $fs $fb;\n  -moz-box-flex: $fg-boxflex;\n  -moz-flex: $fg $fs $fb;\n  -ms-flex: $fg $fs $fb;\n  flex: $fg $fs $fb;\n}\n\n//----------------------------------------------------------------------\n\n// Flexbox Justify Content\n//\n// The 'justify-content' property aligns flex items along the main axis\n// of the current line of the flex container. This is done after any flexible\n// lengths and any auto margins have been resolved. Typically it helps distribute\n// extra free space leftover when either all the flex items on a line are\n// inflexible, or are flexible but have reached their maximum size. It also\n// exerts some control over the alignment of items when they overflow the line.\n//\n// Note: 'space-*' values not supported in older syntaxes.\n//\n// Values: flex-start | flex-end | center | space-between | space-around\n// Default: flex-start\n//\n// http://w3.org/tr/css3-flexbox/#justify-content-property\n\n@mixin justify-content($value: flex-start) {\n  @if $value == flex-start {\n    -webkit-box-pack: start;\n    -ms-flex-pack: start;\n  } @else if $value == flex-end {\n    -webkit-box-pack: end;\n    -ms-flex-pack: end;\n  } @else if $value == space-between {\n    -webkit-box-pack: justify;\n    -ms-flex-pack: justify;\n  } @else if $value == space-around {\n    -ms-flex-pack: distribute;\n  } @else {\n    -webkit-box-pack: $value;\n    -ms-flex-pack: $value;\n  }\n  -webkit-justify-content: $value;\n  -moz-justify-content: $value;\n  justify-content: $value;\n}\n\n// Shorter version:\n@mixin flex-just($args...) {\n  @include justify-content($args...);\n}\n\n//----------------------------------------------------------------------\n\n// Flexbox Align Items\n//\n// Flex items can be aligned in the cross axis of the current line of the\n// flex container, similar to 'justify-content' but in the perpendicular\n// direction. 'align-items' sets the default alignment for all of the flex\n// container's items, including anonymous flex items. 'align-self' allows\n// this default alignment to be overridden for individual flex items. (For\n// anonymous flex items, 'align-self' always matches the value of 'align-items'\n// on their associated flex container.)\n//\n// Values: flex-start | flex-end | center | baseline | stretch\n// Default: stretch\n//\n// http://w3.org/tr/css3-flexbox/#align-items-property\n\n@mixin align-items($value: stretch) {\n  @if $value == flex-start {\n    -webkit-box-align: start;\n    -ms-flex-align: start;\n  } @else if $value == flex-end {\n    -webkit-box-align: end;\n    -ms-flex-align: end;\n  } @else {\n    -webkit-box-align: $value;\n    -ms-flex-align: $value;\n  }\n  -webkit-align-items: $value;\n  -moz-align-items: $value;\n  align-items: $value;\n}\n\n//----------------------------------\n\n// Flexbox Align Self\n//\n// Values: auto | flex-start | flex-end | center | baseline | stretch\n// Default: auto\n\n@mixin align-self($value: auto) {\n  // No Webkit Box Fallback.\n  -webkit-align-self: $value;\n  -moz-align-self: $value;\n  @if $value == flex-start {\n    -ms-flex-item-align: start;\n  } @else if $value == flex-end {\n    -ms-flex-item-align: end;\n  } @else {\n    -ms-flex-item-align: $value;\n  }\n  align-self: $value;\n}\n\n//----------------------------------------------------------------------\n\n// Flexbox Align Content\n//\n// The 'align-content' property aligns a flex container's lines within the\n// flex container when there is extra space in the cross-axis, similar to\n// how 'justify-content' aligns individual items within the main-axis. Note,\n// this property has no effect when the flexbox has only a single line.\n//\n// Values: flex-start | flex-end | center | space-between | space-around | stretch\n// Default: stretch\n//\n// http://w3.org/tr/css3-flexbox/#align-content-property\n\n@mixin align-content($value: stretch) {\n  // No Webkit Box Fallback.\n  -webkit-align-content: $value;\n  -moz-align-content: $value;\n  @if $value == flex-start {\n    -ms-flex-line-pack: start;\n  } @else if $value == flex-end {\n    -ms-flex-line-pack: end;\n  } @else {\n    -ms-flex-line-pack: $value;\n  }\n  align-content: $value;\n}","////\n/// @group color-functions\n/// @since 2.1.5\n/// @author Bernhard Ruoff\n/// @access public\n////\n\n/// sets color based on luminosity of another color\n/// @example\n/// color: setColor(#222222); // returns #fff\n///\n/// @param {Color} $color - Name of base color\n/// @param {Color} $setDark [#000] - Name of dark color\n/// @param {Color} $setLight [#fff] - Name of light color\n///\n/// @return {Color} value of $setLight or $setDark\n\n@function setColor($color, $setDark: #000, $setLight: #fff) {\n  $hue: hue($color);\n\n  @if (opacity($color) > .5) {\n\n    /// if color is not a light color (yellow, cyan)\n    @if ($hue >= 190 or $hue <= 40) {\n      @if (lightness($color) >= 80) {\n        @return $setDark; // Lighter background, return dark color\n      } @else {\n        @return $setLight; // Lighter background, return dark color\n      }\n    } @else {\n      @if (lightness($color) >= 40) {\n        @return $setDark; // Lighter background, return dark color\n      } @else {\n        @return $setLight; // Lighter background, return dark color\n      }\n    }\n  } @else {\n    @return $setDark;\n  }\n}","////\n/// colors.scss - 1.0.0\n/// @author Bernhard Ruoff\n///\n/// color map for xlcss based projects\n/// @param {variable} $colors [$colors] - set a custom variable for each project\n/// @require custom variable for $colors\n///\n/// TODO check for simplification\n/// TODO color function for btn.minor, btn.minorHover, state.hover\n\n// colormap\n$colors: (\n        base: (\n                \"text\": $text,\n                \"background\": $background,\n                \"link\": $link,\n                \"linkHover\": varColor($link, -10),\n                \"super\": $super,\n                \"superHover\": varColor($super, -10),\n                \"highlight\": $highlight,\n                \"divider\": rgba($text, .1),\n                \"border\": borderColor($background, $text)\n        ),\n        btn: (\n                \"default\": $link,\n                \"defaultHover\": varColor($link, -10),\n                \"super\": $super,\n                \"superHover\": varColor($super, -10),\n                \"minor\": rgba($minor, .1),\n                \"minorHover\": rgba($minor, .2),\n                \"pill\": rgba($text, .1),\n                \"pillHover\": rgba($text, .2)\n        ),\n        text: (\n                \"invert\": $background,\n                \"highlight\": $highlight,\n                \"light\": rgba($text, .6),\n                \"lighter\": rgba($text, .4),\n                \"lightest\": rgba($text, .2),\n        ),\n        background: (\n                \"invert\": $text,\n                \"light\": setColor($background, darken($background, 12), lighten($background, 12)),\n                \"lighter\": setColor($background, darken($background, 8), lighten($background, 8)),\n                \"lightest\": setColor($background, darken($background, 4), lighten($background, 4)),\n                \"backdrop\": setColor($background, rgba($text, .8), rgba($background, .8)),\n    